body {
  background-color: #13496D;
  font-size: 1.1rem;
  font-family: "freight-sans-pro";
  font-weight: 400; }

header, .layout-main-wrapper {
  background-color: #fff; }

header .banner {
  padding: 1.1rem 0;
  background: url("../images/header_bottom_bkgrd.svg") 0 100% repeat-x; }
  header .banner .navbar-brand {
    padding: 0; }

.homepage-tiles {
  background-image: url("../images/hero_bkgrd.jpg");
  background-size: cover; }
  .homepage-tiles .block-views {
    font-family: "frank-new";
    background-color: #fff; }
    .homepage-tiles .block-views a {
      font-weight: 400;
      text-decoration: underline; }
    .homepage-tiles .block-views h2 {
      font-size: 1.3rem;
      font-weight: 400; }
      .homepage-tiles .block-views h2 a {
        display: block;
        color: #fff;
        background-color: #13496D;
        padding: .8rem 1.2rem;
        border-bottom: #00BDFF 9px solid; }
      .homepage-tiles .block-views h2 a, .homepage-tiles .block-views h2 a:hover {
        text-decoration: none; }

.mission-statement .statement-container {
  background-color: rgba(23, 54, 75, 0.7);
  width: 80%; }
  .mission-statement .statement-container h1 {
    color: #F4CE90;
    font-family: "rift";
    font-weight: 600; }

.index-col-3 {
  column-count: 3;
  -moz-column-count: 3; }

.menu--main .nav-link {
  color: #13496D;
  font-family: "frank-new";
  font-weight: 500; }

.menu--main .nav-link:hover,
.menu--main .nav-link:focus,
.menu--main .nav-link.active.is-active {
  text-decoration: underline; }

@media (min-width: 1600px) {
  .container {
    max-width: 1540px; }
  .col-xxl-7 {
    max-width: 58.3333333%;
    flex: 0 0 58.3333333%; }
  .col-xxl-5 {
    max-width: 41.66666%;
    flex: 0 0 41.66666%; }
  .col-xxl-4 {
    max-width: 33.3333333%;
    flex: 0 0 33.3333333%; }
  .col-xxl-3 {
    max-width: 25%;
    flex: 0 0 25%; }
  .offset-xxl-3 {
    margin-left: 25%; } }

/* only for retina displays */
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .homepage-tiles {
    background-image: url("../images/hero_bkgrd@2x.jpg"); } }
